@mixin appearance($prop) {
-webkit-appearance: $prop;
   -moz-appearance: $prop;
        appearance: $prop;
}

@mixin background-cover($url, $bgcolor) {
	background: url($url) no-repeat center center fixed $bgcolor; 
	-webkit-background-size: cover;
       -moz-background-size: cover;
  	     -o-background-size: cover;
            background-size: cover;
}

@mixin break($size) {
  @media (max-width: $size) { @content; }
}

@mixin circle($radius) {
	-webkit-border-radius: $radius;
	-moz-border-radius: $radius;
	border-radius: $radius;
	height: $radius;
	width: $radius;
}

@mixin clearfix {
  *zoom: 1;
  &:before, &:after { content: " "; display: table; }
  &:after { clear: both; }
}

@mixin container($width) {
	margin-left: auto; 
	margin-right: auto;
	max-width: $width; 
}

@mixin fullscreen($zindex) {
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	text-align: center;
	width: 100%;
	z-index: $zindex;
}

@mixin linear-gradient($pos, $color1, $color2) {
	background: $color1;
	background: -webkit-linear-gradient($pos, $color1, $color2);
	background: -moz-linear-gradient($pos, $color1, $color2);
	background: linear-gradient($pos, $color1, $color2);
}

@mixin placeholder-color($color) {
	&::-webkit-input-placeholder { color: $color; }
	&:-moz-placeholder { color: $color; }
	&::-moz-placeholder { color: $color; }
	&:-ms-input-placeholder { color: $color; }
}

@mixin rounded-corners($radius) {
	-webkit-border-radius: $radius;
	-moz-border-radius: $radius;
	border-radius: $radius;
}

@mixin scale($size) {
    -ms-transform: scale($size); /* IE 9 */
    -webkit-transform: scale($size); /* Safari */
    transform: scale($size);
}

@mixin text-size-adjust($percent) {
	-ms-text-size-adjust: $percent;
	-webkit-text-size-adjust: $percent;
}

@mixin transition($transition) {
  -webkit-transition: $transition;
     -moz-transition: $transition;
       -o-transition: $transition;
          transition: $transition;
}

@mixin vertical-align {
	position: relative;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
}
