
/* Lists
   ========================================================================== */

ul, li { 
  margin: 0;
  padding: 0;
  list-style:none;
}

ul.styled { 
  font-size: 1.1em; 
  font-weight: 600;
  @include break($mobile) { font-size: 0.9em;}
}

ul.styled li {
  @include transition(all 0.3s);

  border-bottom: 1px solid white;
  cursor: pointer;
  padding: 12px 25px 9px 25px;
  line-height: 1.3em;
  
  &:first-child { border-top: 1px solid white; }
  &:hover { 
    color: $red;
    @include break($mobile) { color: white; }
  }
}
